#mobile-menu__toggle {
  opacity: 0;
}
#mobile-menu__toggle:checked ~ .mobile-menu__btn > span {
  transform: rotate(45deg);
}
#mobile-menu__toggle:checked ~ .mobile-menu__btn > span::before {
  top: 0;
  transform: rotate(0);
}
#mobile-menu__toggle:checked ~ .mobile-menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}
#mobile-menu__toggle:checked ~ .mobile-menu__box {
  visibility: visible;
  right: 0;
}
.mobile-menu {
  &__btn {
    display: flex;
    align-items: center;
    position: fixed;
    top: 20px;
    right: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
  }
  &__btn > span,
  &__btn > span::before,
  &__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #616161;
  }
  &__btn > span::before {
    content: '';
    top: -8px;
  }
  &__btn > span::after {
    content: '';
    top: 8px;
  }
  &__box {
    display: block;
    position: fixed;
    //visibility: hidden;
    top: 0;
    //left: -100%;
    width: 300px;
    height: 100%;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    text-align: center;
    background-color: #ECEFF1;
    box-shadow: 1px 0 6px rgba(0, 0, 0, .2);
  }
  &__item {
    display: block;
    padding: 12px 24px;
    color: #333;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }
  &__item:hover {
    background-color: #CFD8DC;
  }
  &__btn > span,
  &__btn > span::before,
  &__btn > span::after {
    transition-duration: .25s;
  }
  &__box {
    transition-duration: .25s;
  }
  &__item {
    transition-duration: .25s;
  }
}

